/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Archivo", sans-serif;
  font-size: 1.8rem;
  line-height: 1.6;
  background-color: #FEFBFB;
  color: #5B5757;
}

p {
  font-size: 2rem;
  margin: 0;
}

.contenedor {
  width: 80%;
  max-width: 360rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  height: auto;
  display: block;
}

h1, h2, h3 {
  margin: 0;
  font-weight: 500;
  color: #161212;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

#fp-nav ul li a span {
  background-color: #0a0a0a;
}

.grid {
  position: relative;
  z-index: 1;
  display: flex;
  flex-direction: column-reverse;
  justify-content: center;
  max-height: 25rem;
  height: 100%;
}
@media (min-width: 1024px) {
  .grid {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
    justify-content: center;
    align-content: center;
  }
}

.contenedor__texto {
  position: relative;
  z-index: 99;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 2rem;
  row-gap: 0.5rem;
  text-align: center;
}
@media (min-width: 768px) {
  .contenedor__texto {
    row-gap: 2rem;
    padding: 1rem 5rem;
  }
}
.contenedor__texto h1 {
  font-size: 3rem;
  color: #0a0a0a;
}
.contenedor__texto h1 span {
  font-weight: 700;
  font-style: italic;
  color: #FFFAFA;
}
.contenedor__texto p {
  font-size: 1.5rem;
  color: #0a0a0a;
}
@media (min-width: 768px) {
  .contenedor__texto h1 {
    font-size: 8rem;
  }
  .contenedor__texto p {
    font-size: 2rem;
  }
}

.header {
  background: #0a823e;
  background: linear-gradient(135deg, #0a823e 0%, #7cff6b 100%);
}

.contenedor__img {
  height: 30rem;
}
.contenedor__img img {
  height: 100%;
}
@media (min-width: 768px) {
  .contenedor__img {
    height: auto;
  }
  .contenedor__img img {
    height: auto;
  }
}

.contenedor__icono {
  position: absolute;
  bottom: 3%;
  left: 85%;
  z-index: 99;
}
@media (min-width: 768px) {
  .contenedor__icono {
    left: 50%;
    bottom: 3%;
  }
}
.contenedor__icono a i {
  font-size: 6rem;
  color: #fff;
  animation: icono_animation 1.5s ease infinite;
}

.btn-contacto {
  color: #fff;
  cursor: pointer;
  display: block;
  position: relative;
  padding: 0.5rem 2rem;
  border-radius: 2rem;
  border: 2px solid #fff;
  background-color: #216b5e;
  border-radius: 10rem;
  transform: scale(1);
  transition-property: transform;
  transition-duration: 500ms;
}
.btn-contacto:hover {
  color: #fff;
  transform: scale(1.15);
  background-color: transparent;
}

.wave {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 80%;
}

@media (min-width: 768px) {
  .wave {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: auto;
  }
}
@keyframes icono_animation {
  0% {
    transform: translateY(20px);
  }
  25% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(20px);
  }
}
.nav {
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 100;
  width: 100%;
  padding: 2rem 5rem;
}

.nav__logo img {
  width: 6.5rem;
  height: 6.5rem;
}

.menu-mobile {
  display: block;
  cursor: pointer;
}
.menu-mobile i {
  color: #fff;
  font-size: 3rem;
}
@media (min-width: 768px) {
  .menu-mobile {
    display: none;
  }
}

.menu {
  list-style: none;
  display: none;
  row-gap: 1.2rem;
  flex-direction: column;
  align-items: center;
  position: absolute;
  right: 3rem;
  left: 3rem;
  top: 7rem;
  padding: 3rem;
  background-color: rgba(0, 0, 0, 0.6);
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .menu {
    display: flex;
    flex-direction: row;
    background-color: transparent;
    column-gap: 1.2rem;
    position: static;
    padding: 0;
  }
}
.menu a {
  color: #FFFAFA;
  font-size: 2rem;
  transition: 0.5s ease;
}
.menu a:hover {
  color: #0a0a0a;
}
.menu .active {
  border-bottom: 2px solid #FFFAFA;
}
.menu .active a {
  color: #FFFAFA;
}

.menu-active {
  display: flex;
}

.nosotros {
  display: grid;
  grid-template-rows: repeat(2, 24rem);
  align-content: center;
  align-items: center;
  justify-items: center;
}
@media (min-width: 768px) {
  .nosotros {
    height: 100vh;
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
    justify-items: center;
    align-content: center;
    grid-template-rows: auto;
  }
}
.nosotros .nosotros__texto {
  display: flex;
  flex-direction: column;
  place-items: center;
  row-gap: 1rem;
  margin-top: 7rem;
}
@media (min-width: 768px) {
  .nosotros .nosotros__texto {
    margin-top: 0rem;
  }
}
.nosotros .nosotros__texto h1 {
  font-size: 2rem;
  text-transform: uppercase;
  text-align: right;
  line-height: 1.3;
}
.nosotros .nosotros__texto h1 span {
  color: #fff;
}
@media (min-width: 768px) {
  .nosotros .nosotros__texto h1 {
    font-size: 6rem;
  }
}
.nosotros .nosotros__texto p {
  text-align: center;
  font-size: 1.6rem;
}
@media (min-width: 768px) {
  .nosotros .nosotros__texto p {
    font-size: 2rem;
  }
}
.nosotros .nosotros__img {
  height: 20rem;
  border-radius: 1rem;
  width: 80%;
}
@media (min-width: 768px) {
  .nosotros .nosotros__img {
    height: 100vh;
    overflow: hidden;
    width: auto;
  }
}
.nosotros .nosotros__img img {
  object-fit: cover;
  border-radius: 1rem;
  height: 100%;
}
@media (min-width: 768px) {
  .nosotros .nosotros__img img {
    height: 100%;
  }
}

.bg {
  background: #0a823e;
  background: linear-gradient(135deg, #0a823e 0%, #7cff6b 100%);
}

.productos__item {
  display: flex;
  flex-direction: column-reverse;
}
@media (min-width: 768px) {
  .productos__item {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
    justify-items: center;
    align-items: center;
  }
}
.productos__item .item-texto {
  display: flex;
  flex-direction: column;
  place-items: center;
}

/* ------------------------- */
/* Clases de fullpage.js - Flechas de Slide */
/* ------------------------- */
.fp-controlArrow.fp-next,
.fp-controlArrow.fp-prev {
  top: 95%;
}

.fp-controlArrow.fp-prev {
  border-width: 20px 20px 20px 0;
  border-color: transparent #000 transparent transparent;
}
@media (min-width: 768px) {
  .fp-controlArrow.fp-prev {
    left: 80%;
  }
}

.fp-controlArrow.fp-next {
  border-width: 20px 0 20px 20px;
  border-color: transparent transparent transparent #000;
}
@media (min-width: 768px) {
  .fp-controlArrow.fp-next {
    right: 50px;
  }
}

.bg_footer {
  background-color: #F4D03F;
  background-image: linear-gradient(132deg, #F4D03F 0%, #16A085 100%);
}

.footer {
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  align-items: center;
  justify-content: center;
  height: 100vh;
}
@media (min-width: 768px) {
  .footer {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
    justify-items: center;
    align-items: center;
    grid-template-rows: auto;
  }
}
.footer .footer__social {
  margin-top: 10rem;
}
@media (min-width: 768px) {
  .footer .footer__social {
    margin-top: 0;
  }
}
.footer .footer__social div h2 {
  font-size: 3rem;
}
@media (min-width: 768px) {
  .footer .footer__social div h2 {
    font-size: 6rem;
  }
}
.footer .footer__social div p {
  font-size: 1.5rem;
}
@media (min-width: 768px) {
  .footer .footer__social div p {
    font-size: 2rem;
  }
}
.footer .footer__social .social__enlaces {
  display: flex;
  place-items: center;
  column-gap: 1rem;
}
.footer .footer__social .social__enlaces a {
  background-color: #fff;
  border-radius: 50%;
  width: 5rem;
  height: 5rem;
  text-align: center;
}
.footer .footer__social .social__enlaces a i {
  font-size: 5rem;
  color: #0a0a0a;
}

.form_wrap {
  background-color: rgba(73, 72, 72, 0.322);
  border-radius: 10px;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.2);
  width: 90%;
  height: 70%;
  display: none;
}
@media (min-width: 768px) {
  .form_wrap {
    display: block;
    height: auto;
    width: auto;
  }
}

.form_contact {
  padding: 3rem 6rem;
  width: 100%;
}
.form_contact h2 {
  text-align: center;
  font-weight: 600;
  color: #0a0a0a;
  margin-bottom: 3rem;
}
.form_contact .user_info {
  display: flex;
  flex-direction: column;
}
.form_contact label {
  color: #fff;
}
.form_contact input, .form_contact textarea {
  outline: none;
  width: 100%;
  padding: 0.6rem;
  background: none;
  border: none;
  border-bottom: 1px solid #D1D1D1;
}
@media (min-width: 768px) {
  .form_contact input, .form_contact textarea {
    margin-bottom: 2rem;
  }
}
.form_contact textarea {
  max-width: 100%;
  min-width: 100%;
  max-height: 90px;
}
.form_contact input[type=submit] {
  width: 180px;
  border: none;
  align-self: flex-end;
  color: #fff;
  font-size: 16px;
  cursor: pointer;
  color: #fff;
  cursor: pointer;
  display: block;
  position: relative;
  padding: 0.5rem 2rem;
  border-radius: 2rem;
  border: 2px solid #fff;
  background-color: #216b5e;
  border-radius: 10rem;
  transform: scale(1);
  transition-property: transform;
  transition-duration: 500ms;
}
.form_contact input[type=submit]:hover {
  border: 1px solid #fff;
  transition: 1s ease;
  color: #fff;
  transform: scale(1.15);
  background-color: transparent;
}

.overlay {
  background-color: #76be84;
  position: absolute;
  height: 100%;
  right: 0;
  left: 0;
  top: 0;
  bottom: 0;
  display: none;
  z-index: 200;
}

@media (min-width: 768px) {
  .btn-footer {
    display: none;
  }
}

.overlay-active {
  display: flex;
  flex-direction: column-reverse;
  row-gap: 1rem;
  align-items: center;
  justify-content: center;
}

.alerta {
  position: absolute;
  top: 10rem;
  right: 3rem;
  background-color: #0a0a0a;
  padding: 1rem 3rem;
  opacity: 1;
  transition: opacity 2s ease-in-out;
}